body {
  overflow-x: hidden; }

.App {
  width: 100vw;
  height: 100vh;
  text-align: center; }

.section--search-city {
  position: relative;
  min-height: calc(100vh - 60px); }

.section-100vh {
  min-height: calc(100vh - 60px); }

.centerY {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.search {
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: 250px; }

.input--text,
.input--submit {
  border: 0px;
  border-bottom: 2px solid white;
  background-color: transparent;
  color: white;
  display: block;
  width: 250px;
  text-align: left;
  margin-top: 20px;
  font-size: 20px;
  padding-bottom: 5px; }

.input--submit {
  width: auto; }

.footer {
  padding: 20px;
  color: white; }

.weather-for-next__parameters {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); }

.weather-for-next__parameters ul {
  list-style: none; }

.weather-for-next__parameters li {
  text-align: left;
  margin: 0px 40px; }

.padding-top--20px {
  padding-top: 20px; }

.day-name {
  text-align: center; }

.weather-for-next-days {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap; }

.weather-for-next-days__day {
  margin-bottom: 50px;
  width: 34%; }

.chart {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
  max-width: 1100px; }

.weather-parameter-container__main {
  text-align: center;
  width: 25%; }

.weather-icon {
  width: 150px;
  height: 150px; }

.font-size--big {
  font-size: 30px; }

.margin-top--50px {
  margin-top: 50px; }

@media screen and (min-width: 650px) {
  .weather-for-next-days__day {
    width: 33%; } }

@media screen and (min-width: 1000px) {
  .weather-for-next-days {
    max-width: 1100px;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
  .weather-for-next-days__day {
    width: 20%; } }

.white {
  color: white; }

.progress {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin: 0px;
    float: left;
    text-align: center;
    margin-top: 30px
}

.barOverflow {
    /* Wraps the rotating .bar */
    position: relative;
    overflow: hidden;
    /* Comment this line to understand the trick */
    width: 90px;
    height: 45px;
    /* Half circle (overflow) */
    margin-bottom: -14px;
    /* bring the numbers up */
}

.bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 90px;
    height: 90px;
    /* full circle! */
    border-radius: 50%;
    box-sizing: border-box;
    border: 5px solid #262626;
    /* half gray, */
    border-bottom-color: white;
    /* half azure */
    border-right-color: white;
}

@media screen and (min-width: 450px) {
    .progress {
        font-size: 25px;
    }

    .barOverflow {
        width: 150px;
        height: 75px;
    }

    .bar {
        width: 150px;
        height: 150px;
    }
}


